import React from 'react';
import { useEffect, useState } from 'react';
import SilkyDog from '../SilkyDog/SilkyDog';
import Title from '../Title/Title';
import Subtitle from '../Subtitle/Subtitle';
import SocialList from '../SocialList/SocialList';
import BoxBrown from '../BoxBrown/BoxBrown';
import BoxRed from '../BoxRed/BoxRed';

// eslint-disable-next-line react/prop-types
function Main({children}) {

//  
const [windowWidth, setWindowWidth] = useState(window.innerWidth);

const updateWindowSize = () => setWindowWidth(window.innerWidth);
useEffect(() => {
  window.addEventListener('resize', updateWindowSize);
  return () => window.removeEventListener('resize', updateWindowSize);
})

  return (
    <div className="App">
      <main className='App__main'>
        <div className='App__header'>
        <SilkyDog />
        <div className='App__titles'>
          <Title
          />
          <Subtitle/>
        </div>
        <SocialList />
        </div>
        <div className='App__content'>
          <div className='App__boxes'>
          <BoxBrown />
          {windowWidth <= 1050 && <BoxRed />}
          </div>
          {children}
          {windowWidth > 1050 && <BoxRed />}
        </div>
      </main>
    </div>
  );
}

export default Main;
